/**
 * Clients Section Pattern Styles
 */

.pattern-clients-section {
	padding: 4rem 0;
	background: linear-gradient(135deg, #f5f5f5 0%, #ffffff 100%);

	h2 {
		text-align: center;
		font-size: 2.5rem;
		margin-bottom: 3rem;
		color: var(--wp--preset--color--site-color);
	}

	.clients-grid {
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
		gap: 2rem;
		align-items: center;
		justify-items: center;

		.client-logo {
			width: 100%;
			max-width: 150px;
			height: auto;
			opacity: 0.7;
			transition: opacity 0.3s ease, transform 0.3s ease;
			filter: grayscale(100%);

			&:hover {
				opacity: 1;
				transform: scale(1.1);
				filter: grayscale(0%);
			}
		}
	}

	@media (max-width: 768px) {
		.clients-grid {
			grid-template-columns: repeat(2, 1fr);
			gap: 1.5rem;
		}
	}
}
